import {
  Form,
  InputNumber,
  List,
  Space,
  Typography,
  Image,
  Flex,
  Card,
  Radio,
  Input,
  Select,
  Row,
  Col,
  Breadcrumb,
} from "antd";
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { IconDelete } from "../../components/icons";
import { formatNumber } from "../../features/utils";
import { ButtonSubmit } from "../../components/common";

const ListMeta: React.FC<any> = ({ value }) => {
  return (
    <List.Item.Meta
      avatar={
        <Space className="border p-2">
          <Image
            src={value.productImage}
            width={52}
            height={52}
            preview={false}
          />
        </Space>
      }
      title={
        <Link to="/">
          <Typography.Title
            level={5}
            ellipsis={{
              rows: 3,
            }}
          >
            {value.productName}
          </Typography.Title>
        </Link>
      }
      description={<div dangerouslySetInnerHTML={{ __html: value.gift }} />}
    />
  );
};

const Price: React.FC<any> = ({ value }) => {
  return (
    <Typography.Text className="text-basic">
      {formatNumber(value)} 
    </Typography.Text>
  );
};

const productsTest = [
  {
    id: "21212",
    description: {
      productName:
        "My tnh xch tay Dell Latitude 5520 (Intel Core i5.1145G7,15.6inchFHD)My tnh xch tay Dell Latitude 5520 (Intel Core i5.1145G7,15.6inchFHD)",
      productImage: require("../../assets/images/laptop.png"),
      gift: `<ul class="leading-8"><li>VGA ASUS NVIDIA GeForce GT 710 2GB GDDR5</li><li>Chipset: Geforce 710</li><li>B nh: 2Gb DDR5/ 64Bit</li><li>Chun kt ni: PCI Express 2.0</li><li>Cng giao tip: D-Sub/ DVI/ HDMI</li></ul>`,
    },
    price: 8890000,
    slug: "man-hinh-10inch-1",
    quantity: 1,
  },
  {
    id: "21212",
    description: {
      productName:
        "My tnh xch tay Dell Latitude 5520 (Intel Core i5.1145G7,15.6inchFHD)My tnh xch tay Dell Latitude 5520 (Intel Core i5.1145G7,15.6inchFHD)",
      productImage: require("../../assets/images/laptop.png"),
      gift: `<ul class="leading-8"><li>VGA ASUS NVIDIA GeForce GT 710 2GB GDDR5</li><li>Chipset: Geforce 710</li><li>B nh: 2Gb DDR5/ 64Bit</li><li>Chun kt ni: PCI Express 2.0</li><li>Cng giao tip: D-Sub/ DVI/ HDMI</li></ul>`,
    },
    price: 8890000,
    slug: "man-hinh-10inch-1",
    quantity: 1,
  },
];

const ShoppingPage: React.FC = () => {
  const provinceData: any = ["Zhejiang", "Jiangsu"];

  const cityData: any = {
    Zhejiang: ["Hangzhou", "Ningbo", "Wenzhou"],
    Jiangsu: ["Nanjing", "Suzhou", "Zhenjiang"],
  };

  type CityName = keyof typeof cityData;

  const [products, setProducts] = useState(productsTest);
  const [totalPrice, setTotalPrice] = useState<number>(0);
  const [cities, setCities] = useState(cityData[provinceData[0] as CityName]);
  const [secondCity, setSecondCity] = useState(
    cityData[provinceData[0] as CityName][0]
  );

  const onChange = (e: any, eb: any) => {
    console.log(eb);
    const total = eb.products.reduce((accumulator: any, currentValue: any) => {
      return accumulator + currentValue.price * currentValue.quantity;
    }, 0);
    setTotalPrice(total);
    setProducts(eb);
  };
  useEffect(() => {
    const total = products.reduce((accumulator: any, currentValue: any) => {
      return accumulator + currentValue.price * currentValue.quantity;
    }, 0);
    setTotalPrice(total);
  }, []);

  const handleProvinceChange = (value: CityName) => {
    setCities(cityData[value]);
    setSecondCity(cityData[value][0]);
  };

  const onSecondCityChange = (value: CityName) => {
    setSecondCity(value);
  };

  return (
    <Row>
      <Col xs={{ span: 20, offset: 2 }} lg={{ span: 16, offset: 4 }}>
        <Breadcrumb
          items={[
            {
              title: "Home",
            },
            {
              title: "May tinh xach tay",
            },
            {
              title: "Laptop theo hang",
            },
            {
              title: "Laptop lenovo",
            },
          ]}
          className="space"
        />

        <Card className="mb-6">
          <Form
            name="dynamic_form_complex"
            autoComplete="off"
            onValuesChange={onChange}
            initialValues={{ products: products }}
            layout="vertical"
          >
            <Typography.Text italic className="text-basic">
              <span className="text-red-500"> t hng</span>, qu khch hng
              vui lng kim tra sn phm, s lng, gi, mu sc v in cc
              thng tin di y:
            </Typography.Text>
            <Form.List name="products">
              {(fields, { add, remove }) => (
                <>
                  <List
                    itemLayout="horizontal"
                    dataSource={fields}
                    renderItem={(item) => (
                      <List.Item
                        className="!block md:!flex"
                        actions={[
                          <Space
                            onClick={() => {
                              remove(item.name);
                            }}
                            className="hidden md:block mb-6"
                          >
                            <IconDelete />
                          </Space>,
                        ]}
                      >
                        <Form.Item
                          name={[item.name, "description"]}
                          style={{ flex: 1 }}
                        >
                          <ListMeta />
                        </Form.Item>
                        <Space>
                          <Form.Item name={[item.name, "price"]}>
                            <Price />
                          </Form.Item>
                          <Form.Item name={[item.name, "quantity"]}>
                            <InputNumber min={1} max={50} />
                          </Form.Item>
                          <Space
                            onClick={() => {
                              remove(item.name);
                            }}
                            className="block md:hidden mb-6"
                          >
                            <IconDelete />
                          </Space>
                        </Space>
                      </List.Item>
                    )}
                  />
                </>
              )}
            </Form.List>
            <Flex align="center" justify="space-between">
              <Typography.Title level={4}>Tng tin:</Typography.Title>
              <Form.Item name="totalPrice">
                <Typography.Title level={4} type="warning" className="!mb-0">
                  {formatNumber(totalPrice)}
                </Typography.Title>
              </Form.Item>
            </Flex>
            <Card bordered>
              <Typography.Title level={4}>
                1. Thng tin khch hng
              </Typography.Title>
              <Form.Item name="radio-group">
                <Radio.Group>
                  <Radio value="male">Anh</Radio>
                  <Radio value="famale">Ch</Radio>
                </Radio.Group>
              </Form.Item>
              <Form.Item
                name="fullname"
                label="H v tn"
                rules={[{ required: true, message: "Vui lng nhp h v tn" }]}
              >
                <Input placeholder="H v tn" />
              </Form.Item>
              <Form.Item
                name="phone"
                label="S in thoi"
                rules={[
                  { required: true, message: "Vui lng nhp s in thoi" },
                ]}
              >
                <Input placeholder="S in thoi" />
              </Form.Item>
              <Form.Item
                name="email"
                label="Email"
                rules={[{ required: true, message: "Vui lng nhp emai;" }]}
              >
                <Input placeholder="Email( nhn bn sao lu n hng" />
              </Form.Item>
              <Form.Item name="note" label="Ghi ch">
                <Input placeholder="Ghi ch" />
              </Form.Item>
              <Form.Item name="province" label="Tnh/Thnh ph">
                <Select
                  // defaultValue={provinceData[0]}
                  onChange={handleProvinceChange}
                  options={provinceData.map((province: any) => ({
                    label: province,
                    value: province,
                  }))}
                />
              </Form.Item>
              <Form.Item name="city" label="Qun/Huyn">
                <Select
                  value={secondCity}
                  onChange={onSecondCityChange}
                  options={cities.map((city: any) => ({
                    label: city,
                    value: city,
                  }))}
                />
              </Form.Item>
              <Form.Item name="wards" label="X/Phng">
                <Select
                  value={secondCity}
                  onChange={onSecondCityChange}
                  options={cities.map((city: any) => ({
                    label: city,
                    value: city,
                  }))}
                />
              </Form.Item>
              <Form.Item name="address" label="a ch">
                <Input placeholder="a ch" />
              </Form.Item>

              <Typography.Title level={4}>
                2. Chn cch thc nhn hng
              </Typography.Title>
              <Typography.Text className="text-basic">
                Chn phng thc nhn hng s gip bn nhn c sn phm nhanh
                hn
              </Typography.Text>
              <Form.Item name="radio-group">
                <Radio.Group>
                  <Radio value="ship">
                    Giao hng tn ni (C Ph Giao Hng)
                  </Radio>
                  <Radio value="store">Nhn hng ti ca hng</Radio>
                </Radio.Group>
              </Form.Item>

              <Typography.Title level={4}>
                3. Chn hnh thc thanh ton
              </Typography.Title>
              <Form.Item name="radio-group">
                <Radio.Group>
                  <Space direction="vertical">
                    <Radio value="crash">
                      Thanh ton tin mt khi nhn hng (Tin Mt/Qut Th ATM,
                      Visa, Master)
                    </Radio>
                    <Radio value="atm">
                      Thanh ton qua chuyn khon qua ti khon ngn hng
                      (khuyn dng)
                    </Radio>
                    <Radio value="pay">Tr gp c li sut</Radio>
                  </Space>
                </Radio.Group>
              </Form.Item>
            </Card>
            <Form.Item className="mt-6">
              <ButtonSubmit title="t hng" />
            </Form.Item>
          </Form>
        </Card>
      </Col>
    </Row>
  );
};

export default ShoppingPage;
